
import React , { useState , useEffect } from "react";
import './App.css';
import RecipeReviewCard from "./functionImage";

var sum = 0;
function Countercomponent(props){

  const [count , setCount] = useState(5);
 const [toggle , setToggle] = useState(true);
    
    useEffect(()=>{
      console.log("Mounting Phase");
  },[])

  useEffect(()=>{
    console.log("Mount Updating Phase");
},[count])

const toggleFunction = () =>{
  setToggle(!toggle);
}

      return (
        <div className ="App">
         
         {toggle ?
         <>
        <h3>{props.componentName} Component</h3>
        <h4>Initial Value : {count} </h4>
        <button className="App-link" onClick={() => {setCount(count + 1); sum++;}}>Increment</button> &nbsp;
        <button className="App-link" onClick={() => {setCount(count - 1); sum--;}}>Decrement</button> &nbsp;
        <button className="App-link" onClick={() =>{setCount(count - sum); sum = 0;}}>Reset</button>
        </> : <> </>}
        <RecipeReviewCard name="Devan" togglefun = {toggleFunction}/>
        </div>
      );
    }

    export default Countercomponent;